<template>
  <div class="address">
    <van-nav-bar
      title="收货地址"
      left-text="返回"
      right-text="+"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <AddressItem :list="ReceivedAddress"></AddressItem>
  </div>
</template>

<script>
import AddressItem from '@/components/AddressItem.vue'
export default {
  name: 'AddressList',
  data () {
    return {
    }
  },
  created () {
    this.$store.dispatch('address/getReceivedAddressList')
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    onClickRight () {
      this.$router.replace('/editaddress')
    }
  },
  computed: {
    // 收货地址列表
    ReceivedAddress () {
      return this.$store.getters['address/receivedAddress']
    }
  },
  components: {
    AddressItem
  }
}
</script>

