<template>
  <div class="container">
    <div class="item" v-for="item in list" :key="item.address_id">
      <div class="box1">
        <div class="info">
          <p>{{item.name}} {{item.phone}}</p>
          <p>{{item.region.province}} {{item.region.city}} {{item.region.region}} {{item.detail}}</p>
        </div>
        <div class="isDefault">
          <span>是否设置为默认地址 </span>
          <van-switch @click="setDefault(item.address_id)" :value="defaultId===item.address_id" size="14px" />
        </div>
      </div>
      <div class="box2">
        <button @click="editFn(item.address_id)" class="edit">编辑</button>
        <button @click="delFn(item.address_id)" class="del">删除</button>
      </div>
    </div>
  </div>
</template>

<script>
import { setDefaultAddressApi, getDefaultAddressApi } from '@/api/address'
import { Toast } from 'vant'
export default {
  name: 'AddressList',
  props: {
    list: {
      type: Array
    }
  },
  data () {
    return {
      defaultId: -1
    }
  },
  created () {
    this.getDefaultAddress()
  },
  methods: {
    async setDefault (addressId) {
      // 设置addressId为默认地址
      await setDefaultAddressApi(addressId)
      // 然后更改下defaultId为addressId便可使对应的地址按钮亮起
      this.defaultId = addressId
      // console.log(addressId)
      Toast('设置成功')
    },
    async getDefaultAddress () {
      const res = await getDefaultAddressApi()
      // console.log(res.data.defaultId)
      this.defaultId = res.data.defaultId
    },
    editFn (addressId) {
      this.$router.replace({
        path: '/editaddress',
        query: {
          addressId
        }
      })
    },
    delFn (addressId) {
      this.$store.dispatch('address/deleteAddress', addressId)
    }
  }
}
</script>

<style lang="less" scoped>
.item{
  padding: 5px 10px;
  border-bottom: 1px solid #000;
  display: flex;
  justify-content: space-between;
  .box1{
    .info{
    margin-bottom: 3px;
    }
    .isDefault{
      display: flex;
      margin-top: 5px ;
      justify-content: start;
      align-items: center;
      span{
          font-size: 12px;
          margin-right: 5px;
    }
    }
  }
  .box2{
    display:flex;
    flex-direction: column;
    justify-content: center;
    .edit,.del{
      padding: 0;
      border: none;
      background-color: #1989fa;
      color: #fff;
      width: 50px;
      height: 23px;
      line-height: 23px;
      border-radius:5px;
      &:active{
      background-color:rgb(0, 102, 255)
      }
    }
    .edit{
      margin-bottom: 5px;
    }
  }
}

</style>
